@media screen and (min-width: 960px) {
  pre code {
    padding-left: calc(50% - 425px); } }

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: .2rem;
  padding-top: 1px;
  padding-bottom: 1px;
  background: #F5F5F5;
  border: 0px solid #ececec;
  -webkit-font-smoothing: subpixel-antialiased; }

:not(pre) > code {
